import LS from '@/lib/LocalStorage';
import nscCore from '@/lib/core';
import SlackConnect from '@/lib/SlackConnect';

const stateCurrent = () => ({
  isActive: false,
  isAuth: false,
  token: '',
  teamName: '',
  channels: [],
  users: [],
  currentChannel: { id: null }
});

const clientId = '17258488439.50405596566';
const clientSecret = '55775ecb78fe5cfc10250bd0119e0fc5';

// getters
const getters = {};

// actions
const actions = {
  async init({ commit, dispatch }) {
    const code = await LS.getItem('accessCodeSlack');
    const token = await LS.getItem('accessTokenSlack');
    const teamName = await LS.getItem('teamNameSlack');

    if (token) {
      commit('setIsAuth', true);
      commit('setToken', token);
      commit('setTeamName', teamName);
      dispatch('getChannels');
    } else if (code) {
      const oauth = await SlackConnect.request('oauth.access', `client_id=${clientId}&client_secret=${clientSecret}&code=${code}`);
      LS.removeItem('accessCodeSlack');
      LS.setItem('accessTokenSlack', oauth.access_token);
      LS.setItem('teamNameSlack', oauth.team_name);
      commit('setIsAuth', true);
      dispatch('setIsActive');
      commit('setToken', oauth.access_token);
      commit('setTeamName', oauth.team_name);
      dispatch('getChannels');
    }
  },
  async login() {
    const url = 'https://slack.com/oauth/authorize?client_id=17258488439.50405596566&scope=files:write:user,channels:read,users:read';

    await nscCore.tabCreate(url);
  },
  async logout({ commit, rootState }) {
    LS.removeItem('accessCodeSlack');
    LS.removeItem('accessTokenSlack');
    LS.removeItem('teamNameSlack');

    commit('setIsAuth', false);
    commit('setToken', '');
    commit('setIsActive', false);
    commit('core/setDownloadsProgress', false, { root: true });

    if (rootState.nimbus.isAuth) {
      commit('nimbus/setIsActive', true, { root: true });
    }
  },
  async send({ rootState, commit, state }, channelId) {
    const { blob, name, format } = rootState.core.file;
    commit('core/setDownloadsProgress', true, { root: true });

    const fd = new FormData();
    fd.append('token', state.token);
    fd.append('file', blob, `${name}.${format}`);
    fd.append('filename', `Directly uploaded via Nimble capture for Chrome ${name}.${format}`);
    fd.append('initial_comment', rootState.core.file.comment);
    fd.append('channels', channelId || state.currentChannel.id);

    await SlackConnect.sendFile(fd);

    commit('core/setDownloadsProgress', false, { root: true });
  },

  async getChannels({ state, commit }) {
    const slackChannel = await LS.getItem('slackChannel');
    const conversations = await SlackConnect.request('conversations.list', `token=${state.token}`);
    const users = await SlackConnect.request('users.list', `token=${state.token}`);
    // console.log(slackChannel);
    // eslint-disable-next-line no-unsafe-optional-chaining
    const currentChannel = [...conversations?.channels, ...users?.members].find(id => id === slackChannel);

    if (currentChannel) {
      commit('setCurrentChannel', currentChannel);
    }

    commit('setChannels', conversations?.channels || []);
    commit('setUsers', users?.members || []);
    // }
  },
  setIsActive({ commit }) {
    commit('nimbus/setIsActive', false, { root: true });
    commit('youtube/setIsActive', false, { root: true });
    commit('setIsActive', true);
  }
};

// mutations
const mutations = {
  setIsAuth(state, isAuth) {
    state.isAuth = isAuth;
  },
  setToken(state, token) {
    state.token = token;
  },
  setIsActive(state, isActive) {
    state.isActive = isActive;
  },
  setTeamName(state, teamName) {
    state.teamName = teamName;
  },
  setChannels(state, channels) {
    state.channels = channels.sort((a, b) => {
      if (a.name < b.name) return 1;
      if (a.name > b.name) return -1;

      return 0;
    });
  },
  setCurrentChannel(state, currentChannel) {
    console.log(currentChannel);
    LS.setItem('slackChannel', currentChannel.id);
    state.currentChannel = currentChannel;
  },
  setUsers(state, users) {
    state.users = users.sort((a, b) => {
      if (a.name < b.name) return 1;
      if (a.name > b.name) return -1;
      return 0;
    });
  }
};

export default {
  namespaced: true,
  state: stateCurrent,
  getters,
  actions,
  mutations
};
