    <welcome-header />
    <welcome-tabs />
</template>

<script>
import WelcomeTabs from '@/view/welcome/WelcomeTabs';
import WelcomeHeader from '@/view/welcome/WelcomeHeader';

export default {
  name: 'WelcomeIndex',
  components: {
    WelcomeHeader,
    WelcomeTabs
  },
  setup() {},
  mounted() {
    this.$router.push({ path: '/access' });
    document.body.classList.add('nim-capture');
  },
  unmounted() {
    document.body.classList.remove('nim-capture');
  }
};
</script>

