import { createApp } from 'vue';
import { createWebHashHistory, createRouter } from 'vue-router';
import mitt from 'mitt';
import store from '@/store';
import Popper from 'vue3-popper';
import ToastPlugin from 'vue-toast-notification';
import App from '../view/welcome/WelcomeIndex';
import WelcomeSignUpPage from '../view/welcome/tabs/WelcomeSignUpPage';
import WelcomeQuickAccessPage from '../view/welcome/tabs/WelcomeQuickAccessPage';
import WelcomeLetsStartPage from '../view/welcome/tabs/WelcomeLetsStartPage';
import NotFound from '../view/NotFound';
import 'vue-toast-notification/dist/theme-bootstrap.css';

const routes = [
  {
    path: '/',
    name: 'WelcomeSignUpPage',
    component: WelcomeSignUpPage
  },
  {
    path: '/access',
    name: 'WelcomeQuickAccessPage',
    component: WelcomeQuickAccessPage
  },
  {
    path: '/start',
    name: 'WelcomeLetsStartPage',
    component: WelcomeLetsStartPage
  },
  {
    path: '/:catchAll(.*)',
    component: NotFound
  }
];

const router = createRouter({
  history: createWebHashHistory('/welcome.html'),
  routes
});

const emitter = mitt();
const app = createApp(App);
app.config.globalProperties.emitter = emitter;

app.use(router)
  .use(store)
  .use(ToastPlugin)
  .provide('i18n', (message, defaultMessage = '') => chrome.i18n.getMessage(message) || defaultMessage)
  .component('Popper', Popper)
  .mount('body');
