export default class YoutubeConnect {
  static async getPlaylist(token) {
    return fetch('https://www.googleapis.com/youtube/v3/playlists?part=snippet&mine=true&maxResults=50', {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });
  }

  static async getChannels(token) {
    return fetch('https://www.googleapis.com/youtube/v3/channels?part=contentDetails&mine=true&maxResults=50', {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });
  }

  static async uploadTypeResumable(headers, data) {
    return fetch('https://www.googleapis.com/upload/youtube/v3/videos?uploadType=resumable&part=snippet,status,contentDetails', {
      method: 'post',
      headers,
      body: data
    });
  }

  static async sendBlobChunk(url, data, headers) {
    return fetch(url, {
      method: 'put',
      headers,
      body: data
    });
  }
}
