<template>
    <div class="section__info">
        <h3
            class="section__title"
        >
            {{ i18n('welcomeReadyStart') }}
        </h3>
        <p
            class="section__text-blue"
        >
            {{ i18n('welcomeWellDone') }}
        </p>
        <p
            class="section__text"
        >
            {{ i18n('welcomeCompletedAllSteps') }}
        </p>
        <p
            class="section__text"
        >
            {{ i18n('welcomeHereYouGo') }}
        </p>
        <div class="record-buttons">
            <button
                type="button"
                class="primary-btn section__record-btn"
                data-type="screencast"
                @click="recordScreencast"
            >
                {{ i18n('welcomeRecordVideo') }}
            </button>
            <button
                type="button"
                class="primary-btn section__record-btn"
                data-type="screenshot"
                @click="recordScreenshot"
            >
                {{ i18n('welcomeMakeScreenshot') }}
            </button>
        </div>
    </div>
</template>

<script>
import nscCore from '@/lib/core';

export default {
  name: 'StartActionBlock',
  inject: ['i18n'],
  setup() {},
  methods: {
    recordScreencast() {
      nscCore.sendMessage({ operation: 'startScreencast' });
    },
    recordScreenshot() {
      nscCore.sendMessage({ operation: 'startCreateScreenshot', action: 'window', props: { quickCapture: true } });
    }
  }
};
</script>
