<template>
    <header class="nim-capture__header">
        <div class="nim-capture__header-wrapper">
            <div class="nim-capture__header-logo">
                <a
                    href="https://nimblecapture.com/"
                    class="nim-capture__header-link"
                >
                    <img
                        src="~@/images/welcome/nimbus-capture-logo.svg"
                        alt=""
                    >
                </a>
            </div>
            <div class="nim-capture__header-steps">
                <div
                    class="step"
                    :class="{ active: isActiveTab() }"
                    data-step="1"
                >
                    <span class="step__number">1</span>
                    <span class="step__name">Sign Up</span>
                </div>
                <div class="line" />
                <div
                    class="step"
                    :class="{ active: isActiveTab('access') }"
                    data-step="2"
                    path="access"
                >
                    <span class="step__number">2</span>
                    <span class="step__name">Quick access</span>
                </div>
                <div class="line" />
                <div
                    class="step"
                    :class="{ active: isActiveTab('start') }"
                    data-step="3"
                    path="start"
                >
                    <span class="step__number">3</span>
                    <span class="step__name">Let's Start</span>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
export default {
  name: 'WelcomeHeader',
  inject: ['i18n'],
  setup() {},
  methods: {
    isActiveTab(path = '') {
      const currentPath = this.$router.currentRoute.value.path;
      if (!currentPath) {
        return false;
      }
      return currentPath === `/${path}`;
    }
  }
};
</script>
