const headers = {
    'Content-Type': 'application/json',
    'x-client-software': 'screens_chrome',
    'x-api-version': 5,
    Accept: 'application/json'
};

export default class NimbleConnect {
    static nimbleUrl = 'https://share.nimblecapture.com';
    static pending = false;
    static abort = false;

    static async request(params, endpoint, method) {
        this.pending = true;
        try {
            const response = await fetch(
                `${this.nimbleUrl}/${endpoint}`,
                {
                    credentials: 'include',
                    method: method,
                    headers: headers,
                    body: JSON.stringify({ ...params })
                }
            );

            if (this.abort) {
                this.abort = false;
                throw new Error('abort');
            }

            const json = await response.json();

            if (!response.ok) {
                throw new Error(json.message || 'Request failed');
            }

            return json;
        } catch (error) {
            console.error('Error in request:', error);
            throw new Error('Request failed');
        } finally {
            this.pending = false;
        }
    }

    static async getUrls(size, type, name) {
        const uniqueName = `${Date.now()}-${name}`;
        const params = {
            fileName: uniqueName,
            fileSize: size,
            fileType: type,
        };
        return this.request(params, "upload", 'post');
    }

    static async uploadBlob(blob, signedUrl) {
        try {
            const response = await fetch(signedUrl, {
                method: 'PUT',
                body: blob
            });

            if (!response.ok) {
                throw new Error('Upload failed');
            }

            return response;
        } catch (error) {
            console.error('Error uploading blob:', error);
            throw new Error('Upload failed');
        }
    }

    static async saveMedia({ blob, size, type, name }) {
        try {
            const res = await this.getUrls(size, type, name);
            console.log(res);
            await this.uploadBlob(blob, res.signedUrl);
            return res;
        } catch (error) {
            console.error('Error saving media:', error);
            throw new Error('Could not save media');
        }
    }
}
