const GlobalState = () => ({
  file: {
    weight: 0,
    height: 0,
    blob: null,
    url: '',
    origin_url: '',
    size: 0,
    loading: false,
    format: 'webm',
    preConvert: false,
    progressConvert: 0,
    name: '',
    comment: '',
    duration: 0,
    currentTime: 0
  },
  downloads: {
    progress: false,
    url: ''
  },
  challenge: {
    state: '',
    type: '',
    image: ''
  },
  isVideo: false
});

// getters
const getters = {};

// actions
const actions = {};

// mutations
const mutations = {
  setComment(state, comment) {
    state.file.comment = comment;
  },
  setCurrentTime(state, currentTime) {
    state.file.currentTime = currentTime;
  },
  setFileName(state, name) {
    state.file.name = name;
  },
  setFileSize(state, size) {
    state.file.size = size;
  },
  setFileBlob(state, blob) {
    state.file.blob = blob;
  },
  setFileUrl(state, url) {
    state.file.url = url;
  },
  setFileOriginUrl(state, url) {
    state.file.origin_url = url;
  },
  setFileFormat(state, format) {
    state.file.format = format;
  },
  setFileWidth(state, weight) {
    state.file.weight = weight;
  },
  setFileHeight(state, height) {
    state.file.height = height;
  },
  setFileDuration(state, duration) {
    state.file.duration = duration;
  },
  setLoading(state, loading) {
    state.file.loading = loading;
  },
  setIsVideo(state, isVideo) {
    state.isVideo = isVideo;
  },
  setPreConvert(state, preConvert) {
    state.file.preConvert = preConvert;
  },
  setProgressConvert(state, progressConvert) {
    state.file.progressConvert = progressConvert;
  },
  setDownloadsProgress(state, progress) {
    state.downloads.progress = progress;
  },
  setDownloadsUrl(state, url) {
    state.downloads.url = url;
  },
  setChallenge(state, challenge) {
    state.challenge = challenge;
  }
};

export default {
  namespaced: true,
  state: GlobalState,
  getters,
  actions,
  mutations
};
