import LS from '@/lib/LocalStorage';
import nscCore from '@/lib/core';

const MIGRATION_STATUS_STORAGE_KEY = 'NSC_v3_Migration_Done';

export default class StorageMigration {
  static async run() {
    const v3MigrationStatus = await LS.getItem(MIGRATION_STATUS_STORAGE_KEY);

    if (!v3MigrationStatus) {
      const localStorageItems = localStorage ? { ...localStorage } : [];
      const items = await LS.getAllItems();
      // eslint-disable-next-line no-restricted-syntax
      for (const [storageKey, storageItem] of Object.entries(localStorageItems)) {
        if (storageKey === 'watermarkFile') {
          console.log('watermarkFile', storageItem);
          LS.setItem(storageKey, storageItem, true);
        } else {
          try {
            items[storageKey] = JSON.parse(storageItem);
            console.log(storageKey, JSON.parse(storageItem));
          } catch (error) {
            items[storageKey] = storageItem;
            console.log(storageKey, storageItem);
          }
        }
      }

      await LS.setItems(items);
      await nscCore.setTimeout(100);
      console.log('Storage migration to v3 complete.');
      LS.setItem(MIGRATION_STATUS_STORAGE_KEY, true);
    }
  }
}
