import LS from '@/lib/LocalStorage';
import nscCore from '@/lib/core';
import GoogleConnect from '@/lib/GoogleConnect';

const scopes = ['openid email https://www.googleapis.com/auth/drive.metadata.readonly https://www.googleapis.com/auth/drive.file'];

const CurrentState = () => ({
  isAuth: false,
  isActive: false,
  token: '',
  folderCurrent: { id: 'root', title: 'Main folder' },
  folderParent: { },
  folders: [],
  progress: 0
});

// getters
const getters = {};

// actions
const actions = {
  // async init({ commit, dispatch, state }) {
  // const googleToken = await LS.getItem('googleToken');
  //
  // if (googleToken) {
  //   commit('setIsAuth', true);
  //   commit('setToken', googleToken);
  // }
  //
  // if (!state.isAuth) {
  //   const token = await nscCore.getAuthToken({ scopes });
  //
  //   commit('setIsAuth', true);
  //   commit('setToken', token);
  // }
  //
  // dispatch('getFolderCurrent', state.folderCurrent.id);
  // dispatch('getFolderParent', state.folderCurrent.id);
  // dispatch('getFolderList', state.folderCurrent.id);
  // },
  async login({ commit, dispatch, state }, isSend) {
    const activeTab = await nscCore.tabActive(true);

    chrome.identity.getAuthToken(
      { scopes, interactive: true, enableGranularPermissions: true },
      (token, grantedScopes) => {
        if (activeTab) {
          nscCore.tabUpdate(activeTab.id, { active: true, selected: true });
        }

        if (token
          && grantedScopes.includes('openid')
            && grantedScopes.includes('email')
            && grantedScopes.includes('https://www.googleapis.com/auth/drive.metadata.readonly')
            && grantedScopes.includes('https://www.googleapis.com/auth/drive.file')
        ) {
          LS.setItem('googleToken', token);
          commit('setIsAuth', true);
          commit('setToken', token);

          dispatch('getFolderCurrent', state.folderCurrent.id);

          if (isSend) {
            dispatch('send');
          }
          // dispatch('getFolderParent', state.folderCurrent.id);
          // dispatch('getFolderList', state.folderCurrent.id);
        } else {
          dispatch('popup/showPopup', 'GooglePermission', { root: true });
        }
      }
    );
  },
  async logout({ commit, state }) {
    await nscCore.removeAuthToken(state.token);
    LS.removeItem('googleToken');
    commit('setIsAuth', false);
    commit('setToken', '');
    commit('core/setDownloadsProgress', false, { root: true });
  },
  async send({ commit, state, dispatch, rootState }) {
    const length = 1024 * 1024;
    let nextRange = length;
    let blobChunkSend;
    const { format, blob, size, name } = rootState.core.file;
    const mimeType = `${(format === 'mp4' || format === 'webm') ? 'video' : 'image'}/${format}`;

    const data = JSON.stringify({
      name,
      description: `Video uploaded using ${chrome.i18n.getMessage('appName')}`,
      parents: [state.folderCurrent.id]
    });

    let headersSend = {
      Authorization: `Bearer ${state.token}`,
      'x-upload-content-length': size,
      'X-Upload-Content-Type': mimeType
    };

    const sendChunk = async url => {
      commit('core/setDownloadsProgress', Math.ceil(nextRange / size * 100), { root: true });

      const response = await GoogleConnect.sendBlobChunk(url, blobChunkSend, headersSend);

      if (response.status === 308) {
        const range = response.headers.get('range').match(/\d+$/);

        nextRange = range + length;

        if (nextRange > size) {
          nextRange = size;
        }

        headersSend = {
          Authorization: `Bearer ${state.token}`,
          // 'Content-Length': nextRange,
          'Content-Range': `bytes ${range}-${nextRange - 1}/${size}`
        };

        blobChunkSend = blob.slice(range, nextRange);

        await sendChunk(url);
      }
      if (response.status === 200) {
        const json = await response.json();

        commit('core/setDownloadsUrl', `https://drive.google.com/file/d/${json.id}`, { root: true });
        commit('core/setDownloadsProgress', false, { root: true });
      }
    };

    commit('core/setDownloadsProgress', true, { root: true });

    if (size > length) {
      const response = await GoogleConnect.uploadTypeResumable(headersSend, data);
      if (response.status === 200) {
        const location = response.headers.get('Location');

        headersSend = {
          Authorization: `Bearer ${state.token}`,
          // 'Content-Length': length,
          'Content-Range': `bytes 0-${length - 1}/${size}`
        };
        blobChunkSend = blob.slice(0, length);
        await sendChunk(location);
      } else {
        dispatch('logout');
      }
    } else {
      const form = new FormData();
      form.append('metadata', new Blob([data], { type: 'application/json' }));
      form.append('data', blob);

      headersSend = {
        Authorization: `Bearer ${state.token}`
      };

      const response = await GoogleConnect.uploadTypeMedia(headersSend, form);
      if (response.status === 200) {
        const json = await response.json();
        commit('core/setDownloadsUrl', `https://drive.google.com/file/d/${json.id}`, { root: true });
        commit('core/setDownloadsProgress', false, { root: true });
      } else {
        dispatch('logout');
      }
    }
  },
  async getFolderCurrent({ commit, dispatch, state }, id) {
    try {
      if (!state.token) {
        return;
      }
      const response = await GoogleConnect.folderGetInfo(state.token, id);
      commit('setFolderCurrent', response);
    } catch (e) {
      dispatch('logout');
    }
  },
  async getFolderParent({ commit, dispatch, state }, folder) {
    try {
      if (!state.token) {
        return;
      }

      const { items } = await GoogleConnect.folderGetParent(state.token, folder);

      if (items.length !== 0) {
        const [{ id }] = items;
        const responseInfo = await GoogleConnect.folderGetInfo(state.token, id);
        commit('setFolderParent', responseInfo);
      }
    } catch (e) {
      dispatch('logout');
    }
  },
  async getFolderList({ commit, dispatch, state }, current) {
    try {
      if (!state.token) {
        return;
      }

      const responseList = await fetch(
        `https://www.googleapis.com/drive/v2/files/${current}/children?q=mimeType='application/vnd.google-apps.folder'`,
        {
          headers: {
            Authorization: `Bearer ${state.token}`,
            'Content-Type': 'application/json'
          }
        }
      );

      const responseItems = await responseList.json();
      const folders = [];
      // eslint-disable-next-line no-restricted-syntax
      for await (const { id } of responseItems.items) {
        const responseInfo = await GoogleConnect.folderGetInfo(state.token, id);
        folders.push(responseInfo);
      }
      commit('setFolders', folders);
    } catch (e) {
      dispatch('logout');
    }
  }
};

// mutations
const mutations = {
  setIsAuth(state, isAuth) {
    state.isAuth = isAuth;
  },
  setToken(state, token) {
    state.token = token;
  },
  setFolderParent(state, folder) {
    state.folderParent = folder;
  },
  setFolderCurrent(state, folder) {
    state.folderCurrent = folder;
  },
  setFolders(state, folders) {
    state.folders = folders;
  },
  setProgress(state, progress) {
    state.progress = progress;
  }
};

export default {
  namespaced: true,
  state: CurrentState,
  getters,
  actions,
  mutations
};
