export default class GoogleConnect {
  static async folderGetInfo(token, id) {
    const response = await fetch(`https://www.googleapis.com/drive/v2/files/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });

    return response.json();
  }

  static async folderGetParent(token, id) {
    const response = await fetch(`https://www.googleapis.com/drive/v2/files/${id}/parents`, {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json'
      }
    });
    return response.json();
  }

  static async uploadTypeResumable(headers, data) {
    return fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=resumable', {
      method: 'post',
      headers,
      body: data
    });
  }

  static async uploadTypeMedia(headers, data) {
    return fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart', {
      method: 'post',
      headers,
      body: data
    });
  }

  static async sendBlobChunk(url, data, headers) {
    return fetch(url, {
      method: 'put',
      headers,
      body: data
    });
  }
}
