import LS from '@/lib/LocalStorage';
import nscCore from '@/lib/core';
import { Dropbox, DropboxAuth } from 'dropbox';

const redirectUri = 'https://everhelper.me/nimbus-download-image.html';
const clientId = 'os861n2byfevtjy';

const stateCurrent = () => ({
  isAuth: false,
  token: '',
  folders: [],
  folderCurrent: { path: '', name: 'Main folder' }
});

// getters
const getters = {};

// actions
const actions = {
  async init({ commit, dispatch }) {
    const token = await LS.getItem('accessTokenDropbox');

    if (token) {
      commit('setIsAuth', true);
      commit('setToken', token);
      dispatch('getFolders');
    }
  },
  async login() {
    const dbx = new DropboxAuth({ clientId });
    const url = await dbx.getAuthenticationUrl(redirectUri);
    await nscCore.tabCreate(url);
  },
  async logout({ commit, state }) {
    commit('setIsAuth', false);
    commit('setToken', '');

    const dbx = new Dropbox({ accessToken: state.token });
    await dbx.authTokenRevoke();
  },
  async getFolders({ commit, state, dispatch }, patch = '') {
    const dbx = new Dropbox({ accessToken: state.token });
    try {
      const { entries } = await dbx.filesListFolder({ path: patch });
      commit('setFolders', entries);
    } catch (e) {
      dispatch('logout');
    }
  },
  async send({ rootState, commit, state, dispatch }) {
    const { blob, name, format } = rootState.core.file;
    commit('core/setDownloadsProgress', true, { root: true });
    const dbx = new Dropbox({ accessToken: state.token });
    try {
    // eslint-disable-next-line camelcase
      const { result: { path_lower } } = await dbx.filesUpload({
        path: `${state.folderCurrent.path}/${name}.${format}`,
        mode: 'add',
        autorename: true,
        contents: blob
      });
      console.log(path_lower);
      commit('core/setDownloadsProgress', false, { root: true });
      // eslint-disable-next-line camelcase
      commit('core/setDownloadsUrl', `https://www.dropbox.com/home${path_lower}`, { root: true });
    } catch (e) {
      dispatch('logout');
    }
  }
};

// mutations
const mutations = {
  setIsAuth(state, isAuth) {
    state.isAuth = isAuth;
  },
  setToken(state, token) {
    state.token = token;
  },
  setFolders(state, folders) {
    state.folders = folders;
  }
};

export default {
  namespaced: true,
  state: stateCurrent,
  getters,
  actions,
  mutations
};
