import LS from '@/lib/LocalStorage';
import { toRaw } from 'vue';
import nscCore from '@/lib/core';

const CurrentState = () => ({
  isAuthNimbus: false,
  isPremiumNimbus: false,
  isRegNimbus: false,
  isRemindNimbus: false,
  isChallengeNimbus: false,
  isVideoEditor: false,
  isSlackAlert: false,
  isUsageLimitOrg: false,
  isVideoGifNotSupport: false,
  isUpgradePromo: false,
  isUpgradePromoSlide: false,
  isYoutubeNoChannel: false,
  isCnameFree: false,
  isGooglePermission: false,
  isYoutubePermission: false,
  isTranscriptVideo: false,
  isTranscriptVideoFusebase: false
});

const capitalize = s => s && s[0].toUpperCase() + s.slice(1);

// getters
const getters = {};

// actions
const actions = {
  showPopup({ commit, state }, keyCurrent) {
    Object.entries(toRaw(state)).forEach(([key, value]) => {
      if (value) {
        commit(`set${capitalize(key)}`, false);
      }
    });

    if (keyCurrent === 'RegNimbus') {
      // const analytic = '&utm_source=capture&utm_medium=addon&utm_campaign=\'no_account';
      const url = `https://nimblecapture.com/`;

      nscCore.tabCreate(url).then(() => {});
    } else {
      commit(`setIs${capitalize(keyCurrent)}`, true);
    }
  },
  hidePopup({ commit }, keyCurrent) {
    commit(`setIs${capitalize(keyCurrent)}`, false);
  },
};

// mutations
const mutations = {
  setIsAll(state, isSlackAlert) {
    state.isSlackAlert = isSlackAlert;
  },
  setIsSlackAlert(state, isSlackAlert) {
    state.isSlackAlert = isSlackAlert;
  },
  setIsVideoEditor(state, isVideoEditor) {
    state.isVideoEditor = isVideoEditor;
  },
  setIsAuthNimbus(state, isAuthNimbus) {
    state.isAuthNimbus = isAuthNimbus;
  },
  setIsRegNimbus(state, isRegNimbus) {
    state.isRegNimbus = isRegNimbus;
  },
  setIsRemindNimbus(state, isRemindNimbus) {
    state.isRemindNimbus = isRemindNimbus;
  },
  setIsPremiumNimbus(state, isPremiumNimbus) {
    state.isPremiumNimbus = isPremiumNimbus;
  },
  setIsChallengeNimbus(state, isChallengeNimbus) {
    state.isChallengeNimbus = isChallengeNimbus;
  },
  setIsUsageLimitOrg(state, isUsageLimitOrg) {
    state.isUsageLimitOrg = isUsageLimitOrg;
  },
  setIsVideoGifNotSupport(state, VideoGifNotSupport) {
    state.isVideoGifNotSupport = VideoGifNotSupport;
  },
  setIsUpgradePromo(state, UpgradePromo) {
    state.isUpgradePromo = UpgradePromo;
  },
  setIsUpgradePromoSlide(state, UpgradePromoSlide) {
    state.isUpgradePromoSlide = UpgradePromoSlide;
  },
  setIsYoutubeNoChannel(state, YoutubeNoChannel) {
    state.isYoutubeNoChannel = YoutubeNoChannel;
  },
  setIsCnameFree(state, CnameFree) {
    state.isCnameFree = CnameFree;
  },
  setIsGooglePermission(state, GooglePermission) {
    state.isGooglePermission = GooglePermission;
  },
  setIsYoutubePermission(state, YoutubePermission) {
    state.isYoutubePermission = YoutubePermission;
  },
  setIsTranscriptVideo(state, TranscriptVideo) {
    state.isTranscriptVideo = TranscriptVideo;
  },
  setIsTranscriptVideoFusebase(state, TranscriptVideoFusebase) {
    state.isTranscriptVideoFusebase = TranscriptVideoFusebase;
  }
};

export default {
  namespaced: true,
  state: CurrentState,
  getters,
  actions,
  mutations
};
