export default class SlackConnect {
  static async request(action, param) {
    const response = await fetch(`https://slack.com/api/${action}?${param}`);

    return response.json();
  }

  static async sendFile(formData) {
    return fetch('https://slack.com/api/files.upload', {
      method: 'post',
      processData: false,
      contentType: false,
      body: formData
    });
  }
}
