import NimbleConnect from '@/lib/NimbleConnect';
import LS from '@/lib/LocalStorage';

const CurrentState = () => ({
  isAuth: false,
  isPremium: false,
  isActive: false,
  isLoading: false,
  organizations: [],
  workspaces: [],
  notes: [],
  organizationDefault: null,
  workspaceDefault: null,
  noteDefault: null,
  privateLink: '',
  privateChecked: false,
  globalId: '',
  user: {
    authorized: false,
    id: null,
    login: '',
    premium: false,
    usage: {
      current: 0,
      max: 0
    },
    limits: {
      NOTES_MAX_ATTACHMENT_SIZE: 0,
      NOTES_MAX_SIZE: 0,
      NOTES_MONTH_USAGE_QUOTA: 0
    }
  }
});

// getters
const getters = {};

// actions
const actions = {
  async init({ dispatch, state, commit }) {
    commit('setIsLoading', true);

    const isNimbusAuthorized = await LS.getItem('isNimbusAuthorized');
    commit('setIsAuth', isNimbusAuthorized);

    await dispatch('isAuth');
    await dispatch('isPremium');
    if (state.isAuth) {
      await dispatch('setIsActive');
      await dispatch('getInfo');
      await dispatch('getOrganizations');
      await dispatch('getWorkspaces');
      await dispatch('getNotes');
    }
    commit('setIsLoading', false);
  },
  setIsActive({ commit }) {
    commit('slack/setIsActive', false, { root: true });
    commit('youtube/setIsActive', false, { root: true });
    commit('setIsActive', true);
  },
  async send({ commit, rootState, state }) {
    const { file: { blob, size, name, format }, isVideo } = rootState.core;
    commit('core/setDownloadsProgress', true, { root: true });
    const { downloadUrl } = await NimbleConnect.saveMedia({
      blob,
      title: name,
      name: `${name}.${format}`,
      size,
      type: `${isVideo && format !== 'gif' ? 'video' : 'image'}/${format}`,
    });

    commit('core/setDownloadsUrl', downloadUrl, { root: true });
    commit('core/setDownloadsProgress', false, { root: true });
  }
};

// mutations
const mutations = {
  setPrivateChecked(state, privateChecked) {
    state.privateChecked = privateChecked;
  },
  setGlobalId(state, globalId) {
    state.globalId = globalId;
  },
  setPrivateLink(state, privateLink) {
    state.privateLink = privateLink;
  },
  setOrganizations(state, organizations) {
    state.organizations = organizations;
  },
  setWorkspaces(state, workspaces) {
    state.workspaces = workspaces;
  },
  setNotes(state, notes) {
    state.notes = notes;
  },
  setOrganizationDefault(state, organization) {
    state.organizationDefault = organization;
  },
  setWorkspaceDefault(state, workspace) {
    state.workspaceDefault = workspace;
  },
  setNoteDefault(state, note) {
    state.noteDefault = note;
  },
  setInfo(state, user) {
    state.user = { ...state.user, ...user };
  },
  setIsAuth(state, isAuth) {
    state.isAuth = isAuth;
  },
  setIsActive(state, isActive) {
    state.isActive = isActive;
  },
  setIsPremium(state, isPremium) {
    state.isPremium = isPremium;
  },
  setIsLoading(state, isLoading) {
    state.isLoading = isLoading;
  }
};

export default {
  namespaced: true,
  state: CurrentState,
  getters,
  actions,
  mutations
};
