<template>
    <section
        class="section section__pin active"
        data-step="2"
    >
        <div class="section__wrapper">
            <access-action-block />
            <access-image-block />
        </div>
    </section>
</template>

<script>
import LS from '@/lib/LocalStorage';
import nscExt from '@/lib/extension';
import StorageMigration from '@/lib/StorageMigration';
import AccessActionBlock from './access/AccessActionBlock';
import AccessImageBlock from './access/AccessImageBlock';

export default {
  name: 'WelcomeQuickAccessPage',
  components: {
    AccessActionBlock,
    AccessImageBlock
  },
  setup() {},
  async mounted() {
    const localStorageEmpty = !localStorage.length;
    if (localStorageEmpty) {
      await LS.setItem('skipWelcome', true, true);
      await nscExt.openPage(nscExt.welcomePageUrl);
    }

    const migrationResult = await StorageMigration.run();
    if (migrationResult) {
      window.close();
    }
  }
};
</script>
