import LS from '@/lib/LocalStorage';
import nscCore from '@/lib/core';
import YoutubeConnect from '@/lib/YoutubeConnect';

const scopes = ['https://www.googleapis.com/auth/youtube.upload https://www.googleapis.com/auth/youtubepartner'];

const CurrentState = () => ({
  isAuth: false,
  isActive: false,
  token: '',
  fileName: '',
  fileDescription: '',
  progress: 0,
  playlist: []
});

// getters
const getters = {};

// actions
const actions = {
  // async init({ commit, dispatch, state }) {
  //   const youtubeToken = await LS.getItem('youtubeToken');
  //
  //   if (youtubeToken) {
  //     commit('setIsAuth', true);
  //     commit('setToken', youtubeToken);
  //   }
  //
  //   if (!state.isAuth) {
  //     const token = await nscCore.getAuthToken({ scopes });
  //
  //     commit('setIsAuth', true);
  //     commit('setToken', token);
  //   }
  //
  //   dispatch('getPlaylist');
  // },
  setIsActive({ commit }) {
    commit('nimbus/setIsActive', false, { root: true });
    commit('youtube/setIsActive', false, { root: true });
    commit('setIsActive', true);
  },
  async login({ commit, dispatch }, isSend) {
    const activeTab = await nscCore.tabActive(true);

    chrome.identity.getAuthToken(
      { scopes, interactive: true, enableGranularPermissions: true },
      (token, grantedScopes) => {
        if (activeTab) {
          nscCore.tabUpdate(activeTab.id, { active: true, selected: true });
        }

        if (token
          && grantedScopes.includes('https://www.googleapis.com/auth/youtube.upload')
          && grantedScopes.includes('https://www.googleapis.com/auth/youtubepartner')
        ) {
          LS.setItem('youtubeToken', token);
          commit('setIsAuth', true);
          commit('setToken', token);

          dispatch('setIsActive');
          dispatch('getPlaylist');

          if (isSend) {
            dispatch('send');
          }
        } else {
          dispatch('popup/showPopup', 'YoutubePermission', { root: true });
        }
      }
    );
  },
  async logout({ commit, state, rootState }) {
    await nscCore.removeAuthToken(state.token);
    LS.removeItem('youtubeToken');

    commit('setIsAuth', false);
    commit('setToken', '');
    commit('setIsActive', false);
    commit('core/setDownloadsProgress', false, { root: true });

    if (rootState.nimbus.isAuth) {
      commit('nimbus/setIsActive', true, { root: true });
    }
  },
  async send({ commit, state, dispatch, rootState }) {
    const length = 1024 * 1024;
    let blobChunkSend;
    const { format, blob, size } = rootState.core.file;
    const mimeType = `${(format === 'mp4' || format === 'webm') ? 'video' : 'image'}/${format}`;
    const shareOnYoutube = await LS.getItem('shareOnYoutube');

    const data = JSON.stringify({
      snippet: {
        title: state.fileName,
        description: state.fileDescription,
        categoryId: '22'
      },
      status: {
        privacyStatus: shareOnYoutube
      }
    });

    let headers = {
      Authorization: `Bearer ${state.token}`,
      'x-upload-content-length': size,
      'X-Upload-Content-Type': mimeType
    };

    commit('core/setDownloadsProgress', 0, { root: true });
    let response = await YoutubeConnect.uploadTypeResumable(headers, data);

    if (response.status !== 200) {
      commit('core/setDownloadsProgress', false, { root: true });
      dispatch('logout');
    } else {
      const location = response.headers.get('Location');

      const forChunk = async range => {
        response = await YoutubeConnect.sendBlobChunk(location, blobChunkSend, headers);

        if (response.status === 308) {
          let nextRange = range + length;

          if (nextRange > size) {
            nextRange = size;
          }

          headers = {
            Authorization: `Bearer ${state.token}`,
            'Content-Length': nextRange,
            'Content-Range': `bytes ${range}-${nextRange - 1}/${size}`
          };
          commit('core/setDownloadsProgress', Math.ceil(nextRange / size * 100), { root: true });
          blobChunkSend = blob.slice(range, nextRange);

          await forChunk(nextRange);
        }
        if (response.status === 200) {
          console.log(response);
          const json = await response.clone().json();
          console.log(json);
          commit('core/setDownloadsUrl', `https://www.youtube.com/watch?v=${json.id}`, { root: true });
          commit('core/setDownloadsProgress', false, { root: true });
        }
      };

      if (size > length) {
        headers = {
          Authorization: `Bearer ${state.token}`,
          'Content-Length': length,
          'Content-Range': `bytes 0-${length - 1}/${size}`
        };
        blobChunkSend = blob.slice(0, length);
      } else {
        headers = { Authorization: `Bearer ${state.token}` };
      }

      await forChunk(length);
    }
  },
  async getPlaylist({ commit, dispatch, state }) {
    try {
      if (!state.token) {
        return;
      }
      let response = await YoutubeConnect.getPlaylist(state.token);
      let data = await response.json();
      if (response.status !== 200) {
        response = await YoutubeConnect.getChannels(state.token);
        data = await response.json();
        if (!data.pageInfo.totalResults.length) {
          dispatch('popup/showPopup', 'YoutubeNoChannel', { root: true });

          dispatch('logout');
        }
      }
      commit('setPlaylist', data.items.reverse());
    } catch (e) {
      dispatch('logout');
    }
  }
};

// mutations
const mutations = {
  setIsAuth(state, isAuth) {
    state.isAuth = isAuth;
  },
  setIsActive(state, isActive) {
    state.isActive = isActive;
  },
  setToken(state, token) {
    state.token = token;
  },
  setPlaylist(state, playlist) {
    state.playlist = playlist;
  },
  setProgress(state, progress) {
    state.progress = progress;
  },
  setFileName(state, fileName) {
    state.fileName = fileName;
  },
  setFileDescription(state, fileDescription) {
    state.fileDescription = fileDescription;
  }
};

export default {
  namespaced: true,
  state: CurrentState,
  getters,
  actions,
  mutations
};
